<template>
    <div>
        <div v-if="transportType === 'bus'">
            <p><span class="mdi mdi-access-point"></span> {{ liveLocation }}</p>
        </div>
    </div>
</template>

<script>
import { baseEndpoint } from "../../js/config.js";

var timer;
var stops = [];
var startLiveAtTime = -1;

export default {
    props: ["step"],
    data() {
        return {
            transportType: "",
            liveLocation: "",
            busCode: ""
        }
    },
    methods: {
        async getData() {
            var transitDetails = Object.assign({}, this.step.transit_details);
            var transportType = transitDetails.line.vehicle.name.toLowerCase();
            var departureStop = transitDetails.departure_stop.location;

            this.transportType = transportType;

            if (transportType === "bus") {
                if (!this.busCode) {
                    stops = await fetch(baseEndpoint + "/busStop?latLng=" + departureStop.lat + "," + departureStop.lng).then(r => r.json());

                    stops = stops.busStops;

                    var stop = stops[0];

                    var passes = Object.entries(stop.Passes);

                    // var expectedTime = this.toTime(transitDetails.departure_time.value);
                    var googleTime = new Date(0);
                    googleTime.setUTCSeconds(transitDetails.departure_time.value);

                    for (var pass of passes) {
                        var passData = pass[1];

                        console.log(passData);

                        var ovApiTime = new Date(passData.TargetDepartureTime);

                        var distance = ovApiTime.getTime() - googleTime.getTime();

                        console.log(distance);

                        if (distance >= -450000 && distance <= 450000) {
                            this.busCode = pass[0];
                        }
                    }
                }

                if (this.busCode) {
                    var now = new Date(Date.now());

                    if (startLiveAtTime === -1 || startLiveAtTime >= now - 30000) {
                        var busCode = this.busCode;

                        var busPassStops = await fetch(baseEndpoint + "/busJourney/" + busCode).then(r => r.json());

                        var findNextStop = stops => {
                            for (var stop of stops) {
                                if (stop.TripStopStatus === "DRIVING" || stop.TripStopStatus === "ARRIVED") {
                                    return stop;
                                }
                            }

                            return false;
                        }

                        var stops = Object.values(busPassStops.Stops);
                        // console.log(stops[0]);
                        var nextStop = findNextStop(stops);

                        console.log(nextStop);

                        if (nextStop) {
                            if (nextStop.TripStopStatus === "DRIVING") {
                                this.liveLocation = "Rijd naar " + nextStop.TimingPointName;
                            } else if (nextStop.TripStopStatus === "ARRIVED") {
                                this.liveLocation = "Aangekomen bij " + nextStop.TimingPointName;
                            } else {
                                this.liveLocation = "Geen info";
                            }
                        } else {
                            this.liveLocation = "Bus nog niet aan het rijden";
                            
                            let time = new Date(stops[0].ExpectedArrivalTime);

                            startLiveAtTime = time.getTime();
                        }
                    }
                } else {
                    this.liveLocation = "Geen data beschikbaar";
                }                
            }
        },
        toTime(timestamp) {
            var timeDate = new Date(0);
            timeDate.setUTCSeconds(timestamp);

            var minutes = timeDate.getMinutes();

            if (minutes < 10) {
                minutes = "0" + minutes;
            }

            return timeDate.getHours() + ":" + minutes;
        }
    },
    created() {
        this.getData();

        timer = setInterval(() => {
            this.getData();
        }, 30000);
    },
    destroyed() {
        console.log("CLEARED")
        clearInterval(timer);
    }
}
</script>

<style lang="scss" scoped>

$hFont: "Roboto Mono", monospace;
$pFont: "Roboto", sans-serif;

p {
    font-size: 18px;
    font-family: $pFont;
    color: #000000;
    mix-blend-mode: difference;

    .mdi.mdi-access-point {
        color: #429200;
    }
}

</style>