<template>
    <div class="routeGuide">
        <section class="currentStep" :class="{ routeStarted: routeStarted }">
            <div v-if="routeStarted">
                <h1>
                    <span :class="tripStepIcon(currentStep)"></span>
                    {{ currentStep.info }}
                </h1>
                <routeStep class="current" :step="currentStep" />
            </div>
            <div v-else>
                <h1>Geen reis gestart</h1>
            </div>
        </section>
        <section class="upcommingSteps" v-if="routeStarted">
            <routeStep :step="step" v-for="step in tripProgress" :key="step.id" />
            <div class="endStep">
                <p>
                    <span class="mdi mdi-flag-checkered"></span>
                    {{ toTime(route.directions.arrival_time.value) }} {{ route.directions.endName }}
                </p>
            </div>
        </section>
    </div>
</template>

<script>
import routeStep from "./routeStep.vue";
import vueChannel from "vue-channel";
import { db } from "../../js/config.js";

import { toTime, tripStepIcon, toDurationFormat } from "./shared.js";

export default {
    components: {
        routeStep
    },
    data() {
        return {
            routeStarted: false,
            currentStepIndex: -1,
            pos: {
                lat: 0,
                long: 0
            },
            route: {}
        };
    },
    computed: {
        currentStep() {
            this.updateTripIndex();

            return this.route.directions.steps[this.currentStepIndex];
        },
        tripProgress() {
            this.updateTripIndex();

            var steps = Object.assign([], this.route.directions.steps);

            steps = steps.slice(this.currentStepIndex + 1, Infinity);

            return steps;
        },
        extraInfoAvailable() {
            return this.currentStep.travel_mode !== "WALKING";
        },
    },
    methods: {
        tripStepIcon,
        toTime,
        toDurationFormat,
        updateTripIndex() {
            var route = Object.assign({}, this.route);

            var steps = route.directions.steps;

            steps = steps.map((step, index) => {
                var distanceLat = this.pos.lat - step.start_location.lat;
                var distanceLong = this.pos.long - step.start_location.lng;

                var directDistance = Math.sqrt(
                    distanceLat ** 2 + distanceLong ** 2
                );

                return {
                    ...step,
                    index,
                    directDistance
                };
            });

            var stepsSorted = steps.sort(
                (a, b) => a.directDistance - b.directDistance
            );

            // if (this.currentStepIndex > -1) {
            //     console.log(stepsSorted);

            //     var stepsWithinRange = stepsSorted.filter(step => step.directDistance <= 0.001);
               
            //     if (stepsWithinRange.length > 0) {
            //         this.currentStepIndex = stepsWithinRange[0].index
            //     } else {
            //         this.currentStepIndex = stepsSorted[0].index
            //     }
            // } else {
            //     this.currentStepIndex = stepsSorted[0].index;
            // }

            this.currentStepIndex = 1;
        },
    },
    created() {
        vueChannel("route").receive(state => {
            if (state.routeStarted) {
                this.route = Object.assign({}, this.route, state.route);

                this.routeStarted = true;
            } else {
                this.routeStarted = false;
            }
        });

        vueChannel("pos").receive(state => {
            this.pos.lat = state.latitude;
            this.pos.long = state.longitude;
        });

        if (db.route) {
            this.route = Object.assign({}, this.route, db.route);
            this.routeStarted = true;
        }
    }
};
</script>

<style lang="scss" scoped>
@import "../../scss/config.scss";

.currentStep {
    width: calc(95% - 32px);

    padding: 16px;
    margin: 20px auto;
    margin-bottom: 0;

    border-radius: 10px;
    box-shadow: 0px 0px 59px -7px rgba(128, 128, 128, 0.8);

    &.routeStarted {
        padding-bottom: 0;
    }

    h1 {
        margin: 0;

        font-size: 22px;
        font-family: $hFont;
        color: #000000;
        mix-blend-mode: difference;
    }

    p {
        margin: 0;
        margin-top: 8px;

        font-size: 18px;
        font-family: $pFont;
        color: #000000;
        mix-blend-mode: difference;
    }
}

.upcommingSteps {
    width: calc(95% - 62px);

    margin: 0px auto;
}

.endStep {
    min-height: 16px;
    width: calc(100% - 32px);

    padding: 4px 16px;

    border-left: 2px solid transparent;

    p {
        font-size: 20px;
        font-family: $pFont;
    }
}
</style>