import { baseEndpoint } from "./config.js";

function nsCall(endpoint, queryObject) {
    return new Promise((res, rej) => {
        endpoint = baseEndpoint + endpoint + "?apiToken=global";

        if (queryObject !== undefined) {
            var queryArguments = Object.entries(queryObject);

            queryArguments.forEach(argument => {
                endpoint += "&" + argument[0] + "=" + argument[1];
            });
        }

        console.log("fetch to", endpoint);

        fetch(endpoint)
            .then(r => {
                return r.json()
            })
            .then(json => res(json))
            .catch(err => rej(err));
    });
}

export default nsCall;