<template>
    <page>
        <header>
            <h1>AUT-OV</h1>
        </header>
        <main class="mainPage">
            <routeGuide />
            <div style="margin-top: 20px;" class="divider"></div>
            <section class="liveRoute">
                <h1>Routes</h1>
                <routeOption
                    :route="route"
                    v-for="route in routes"
                    :key="route.id"
                />
                <div class="dropDown">
                    <p v-if="!routeButtonsOpen" @click="routeButtonsOpen = true"><span class="mdi mdi-chevron-down"></span> meer opties</p>
                    <p v-else @click="routeButtonsOpen = false"><span class="mdi mdi-chevron-up"></span> minder opties</p>
                    <div class="dropDownContent" v-if="routeButtonsOpen">
                        <button @click="refreshRoutes()"><span class="mdi mdi-refresh"></span> Refresh</button>
                        <div class="halfButtons">
                            <button @click="setRouteTo('home')">
                                <span class="mdi mdi-home-variant-outline"></span>
                                Huis
                            </button>
                            <button @click="setRouteTo('work')">
                                <span class="mdi mdi-briefcase-outline"></span>
                                Werk
                            </button>
                        </div>
                    </div>
                </div>
            </section>
            <div class="divider"></div>
            <dynamicInfo />
            <div class="divider"></div>
            <section class="userSettings">
                <h1>Instellingen</h1>
                <input type="text" placeholder="Thuis address" v-model="address.home.query" @keyup.enter="addressQueryResult('home')" />
                <div class="locationQuery">
                    <div class="locationQueryResult" @click="chooseAddress('home', result)" :class="[ result.geometry.type ]" v-for="result in address.home.queryResult" :key="result.id">
                        <p>{{ result.properties.search }}</p>
                    </div>
                </div>
                <input type="text" placeholder="Werk address" v-model="address.work.query" @keyup.enter="addressQueryResult('work')" />
                <div class="locationQuery">
                    <div class="locationQueryResult" @click="chooseAddress('work', result)" :class="[ result.geometry.type ]" v-for="result in address.work.queryResult" :key="result.id">
                        <p>{{ result.properties.search }}</p>
                    </div>
                </div>
                <button @click="queryBothAddresses()">
                    search
                </button>
            </section>
            <p>
                Lat: {{ pos.lat }}
                Long: {{ pos.long }}
                Route to: {{ routeTo }}
            </p>
        </main>
    </page>
</template>

<script>
import vueChannel from "vue-channel";

import page from "../components/page.vue";
import routeOption from "./dashboard/routeOption.vue";
import routeGuide from "./dashboard/routeGuide.vue";
import dynamicInfo from "./dashboard/dynamicInfo.vue";

import { baseEndpoint, db } from "../js/config.js";

import nsCall from "../js/nsCall.js";

import testGoogle from "../json/testGoogle.json";

function getStationCodeFromQuery(query) {
    var exactMatchAvailableAt = -1;
    var matchedStation = {};
    var queried = stationsQueryable.queryable.filter((station, index) => {
        if (station.stationName.toLowerCase() === query) {
            exactMatchAvailableAt = index;
        }

        return station.stationName.toLowerCase().includes(query);
    });

    if (exactMatchAvailableAt !== -1) {
        console.log(exactMatchAvailableAt);
        matchedStation = stationsQueryable.queryable[exactMatchAvailableAt];
    } else {
        matchedStation = queried[0];
    }

    return matchedStation;
}

export default {
    components: {
        page,
        routeOption,
        routeGuide,
        dynamicInfo
    },
    data() {
        return {
            routes: [],
            pos: {
                lat: 0,
                long: 0
            },
            address: {
                home: {
                    query: "",
                    queryResult: [],
                    pos: {
                        lat: 0,
                        long: 0
                    }
                },
                work: {
                    query: "",
                    queryResult: [],
                    pos: {
                        lat: 0,
                        long: 0
                    }
                }
            },
            routeTo: "none",
            routeButtonsOpen: false
        };
    },
    computed: {
        filteredMaterialParts() {
            return this.queryResult.materieeldelen.filter(
                part => part.materieelnummer !== undefined
            );
        },
        currentSteps() {
            var steps = this.route.directions.steps;

            var withinRangeOfSteps = steps.filter(step => {});
        }
    },
    methods: {
        setRouteTo(address) {
            this.routeTo = address;
            db.routeTo = address;

            this.refreshRoutes();
        },
        refreshRoutes() {
            this.routes = [];

            this.getRoutes();
        },
        queryBothAddresses() {
            this.addressQueryResult("home");
            this.addressQueryResult("work");
        },
        async addressQueryResult(address) {
            var query = this.address[address].query;

            if (query.length > 5) {
                var results = await fetch(`https://1313.nl/geocoder/${query}*`)
                    .then(r => r.json());

                console.log("res", results);

                var features = results.features.filter(feature => {
                    console.log(
                        feature.geometry.coordinates[0] !== this.address[address].pos.long,
                        feature.geometry.coordinates[1] !== this.address[address].pos.lat
                    );

                    return feature.geometry.coordinates[0] !== this.address[address].pos.long && feature.geometry.coordinates[1] !== this.address[address].pos.lat;
                });

                this.address[address].queryResult = features.map(feature => {
                    feature.id = Math.random();

                    return feature;
                });
            } else {
                this.address[address].queryResult = [];
            }
        },
        chooseAddress(address, result) {
            this.address[address].query = result.properties.search;

            this.address[address].pos.long = result.geometry.coordinates[0];
            this.address[address].pos.lat = result.geometry.coordinates[1];

            this.address[address].queryResult = [];

            db[address] = this.address[address];
        },
        performQuery() {
            var query = this.query.toLowerCase();
            this.resultsError = false;

            if (!isNaN(query)) {
                nsCall("/virtualTrain/" + this.query)
                    .then(responce => {
                        console.log(responce);

                        this.resultType = "train";
                        this.queryResult = Object.assign(
                            {},
                            this.queryResult,
                            responce
                        );
                    })
                    .catch(err => {
                        this.resultType = "error";
                        this.queryResult = Object.assign({}, this.queryResult, {
                            err
                        });
                    });
            } else {
                query = query.split(" to ");

                // console.log(getStationCodeFromQuery(query[0]));
                // console.log(getStationCodeFromQuery(query[1]));
                nsCall("/trips", {
                    fromStation: getStationCodeFromQuery(query[0]).stationCode,
                    toStation: getStationCodeFromQuery(query[1]).stationCode
                }).then(responce => {
                    console.log(responce);
                });
            }
        },
        facilityIcon(facility) {
            if (facility === "WIFI") {
                return ["mdi-wifi"];
            } else if (facility === "TOILET") {
                return ["mdi-toilet"];
            } else if (facility === "STILTE") {
                return ["mdi-volume-off"];
            } else if (facility === "FIETS") {
                return ["mdi-bicycle"];
            } else if (facility === "STROOM") {
                return ["mdi-power-plug"];
            }
        },
        async getRoutes(recallIteration = 0) {
            if (this.routeTo === "work") {
                var pos = this.address.work.pos;
            } else {
                var pos = this.address.home.pos;
            }

            var toLatLong = pos.lat + "," + pos.long;
            var fromLatLong = this.pos.lat + "," + this.pos.long;

            console.log(
                fromLatLong,
                "to",
                toLatLong
            );

            if (fromLatLong === "0,0" || toLatLong === "0,0") {
                recallIteration++;
                
                if (recallIteration < 5) {
                    setTimeout(() => {
                        this.getRoutes(recallIteration);
                    }, 1000);
                }

                return;
            }

            var response = await fetch(baseEndpoint + `/trips?fromLatLong=${fromLatLong}&toLatLong=${toLatLong}`).then(r => r.json());

            // var response = testGoogle;
            console.log(response);

            this.routes = response.routes.map(route => {
                route.id = Math.random();

                route.directions = route.legs[0];

                var startName = route.directions.start_address.split(", ");
                startName.pop();
                startName.pop();
                startName = startName.join(", ");

                route.directions.startName = startName;

                var endName = route.directions.end_address.split(", ");
                endName.pop();
                endName.pop();
                endName = endName.join(", ");

                route.directions.endName = endName;

                const isTrainTransit = step => {
                    return step.travel_mode === "TRANSIT" && step.transit_details.line.vehicle.name.toLowerCase() === "train";
                }

                route.directions.steps = route.directions.steps
                    .filter((step, index, array) => {
                        var betweenSteps = array[index - 1] !== undefined && array[index + 1] !== undefined;
                        var betweenTrains = false;

                        if (betweenSteps) {
                            betweenTrains = isTrainTransit(array[index - 1]) && isTrainTransit(array[index + 1]);
                        }

                        if (betweenSteps && betweenTrains) {
                            return true;
                        } else if (step.travel_mode === "WALKING" && step.duration.value <= 120) {
                            route.directions.duration.value -= step.duration.value;

                            return false;
                        }

                        return true;
                    })
                    .map((step, index, array) => {
                        step.id = Math.random();

                        step.info = step.html_instructions;

                        step.info = step.info.replace("Walk to ", "");
                        step.info = step.info.replace("Bus towards ", "");
                        step.info = step.info.replace("Train towards ", "");

                        step.open = false;

                        var betweenSteps = array[index - 1] !== undefined && array[index + 1] !== undefined;
                        var betweenTrains = false;

                        if (betweenSteps) {
                            betweenTrains = isTrainTransit(array[index - 1]) && isTrainTransit(array[index + 1]);
                        }

                        if (betweenSteps && betweenTrains) {
                            step.travel_mode = "TRANSFER";
                        }

                        return step;
                    });

                var totalForCalculation = route.directions.steps
                    .map(step => step.duration.value)
                    .reduce((a, b) => a + b);

                route.directions.totalForCalculation = totalForCalculation;

                return route;
            });
        },
        peek(index) {
            var currentIndex = this.currentStepIndex;

            this.currentStepIndex = index;

            setTimeout(() => {
                this.currentStepIndex = currentIndex;
            }, 3000);
        }
    },
    created() {
        var dynamicRefreshLimit = 20;
        var currentRefreshCount = 0;

        if ("permissions" in navigator) {
            navigator.permissions
                .query({ name: "geolocation" })
                .then(response => {
                    this.acceptedGeolocation = response.status === "granted";

                    navigator.geolocation.watchPosition(({ coords }) => vueChannel("pos").set(coords));
                    navigator.geolocation.getCurrentPosition(({ coords }) => vueChannel("pos").set(coords));
                });
        }

        const loadAddress = addressName => {
            if (db[addressName]) {
                this.address[addressName] = Object.assign({}, this.address[addressName], db[addressName])
            }
        };

        loadAddress("home");
        loadAddress("work");

        if (db.routeTo) {
            this.routeTo = db.routeTo;
        }

        vueChannel("pos").receive(state => {
            this.pos.lat = state.latitude;
            this.pos.long = state.longitude;

            var directDistanceHome = Math.sqrt((this.address.home.pos.lat - state.latitude) ** 2 + (this.address.home.pos.long - state.longitude) ** 2);
            var directDistanceWork = Math.sqrt((this.address.work.pos.lat - state.latitude) ** 2 + (this.address.work.pos.long - state.longitude) ** 2);

            var lastRouteTo = this.routeTo;

            if (this.routeTo === "none") {
                if (directDistanceHome < directDistanceWork) {
                    this.routeTo = "work";
                } else {
                    this.routeTo = "home";
                }

                db.routeTo = this.routeTo;
            } else {
                if (this.routeTo === "home" && directDistanceHome < 0.0004) {
                    this.routeTo = "work";
                } else if (this.routeTo === "work" && directDistanceWork < 0.0004) {
                    this.routeTo = "home";
                }
            }

            if (this.routeTo !== lastRouteTo) {
                this.getRoutes();
            }
        });

        this.getRoutes();
    }
};
</script>

<style lang="scss" scoped>
@import "../scss/config.scss";

// box-shadow: 0px 0px 59px -7px rgba(128,128,128,0.8);

.inner > header {
    width: 100%;
    height: 64px;

    background-color: #000000;
    // box-shadow: 0px 0px 59px -7px rgba(128,128,128,0.8);

    h1 {
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        margin: 0;
        padding: 0px 16px;

        font-size: 24px;
        color: #ffffff;
        font-family: $hFont;
        font-weight: 700;
    }
}

main.mainPage {
    h2 {
        font-family: $hFont;
        padding: 0px 16px;
    }

    input {
        width: calc(95% - 32px);

        display: block;
        margin: 8px auto;
        padding: 8px;

        font-size: 18px;
        font-family: $hFont;
        outline: 0;
        border-radius: 5px;
        border: 2px solid #000000;
        color: #000000;
    }

    button {
        width: calc(95% - 16px);

        display: block;
        margin: 8px auto;
        padding: 12px 8px;

        font-size: 18px;
        outline: 0;
        border-radius: 5px;
        background-color: #000000;
        border: 0;
        color: #ffffff;
        cursor: pointer;
    }

    .halfButtons {
        width: calc(95% - 16px);
        
        display: inline-block;
        position: relative;
        left: 50%;
        transform: translateX(-50%);

        button {
            width: 50%;

            float: left;
            margin: 8px 0;

            &:first-child {
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
                
                & + button {
                    border-top-left-radius: 0;
                    border-bottom-left-radius: 0;
                }
            }
        }
    }

    .divider {
        height: 0px;
        width: 100%;

        border-top: 2px dashed #cecece;
    }

    .liveRoute {
        width: 95%;

        margin: 0 auto;
    }

    section > h1 {
        margin: 16px 0;

        text-align: center;
        font-family: $hFont;
        font-weight: 400;
    }

    .stationDepartures {
        border-top: 1px solid #d3d3d3;
        border-bottom: 1px solid #d3d3d3;

        h1 {
            margin: 8px auto;

            text-align: center;
            font-family: "Roboto", sans-serif;
            font-weight: 400;
            color: #363636;
        }

        .departure {
            padding: 8px 16px;

            cursor: pointer;

            p {
                margin: 0;

                font-family: "Roboto", sans-serif;
                font-weight: 500;

                span {
                    font-weight: 700;

                    &.actualTime {
                        color: #099910;
                    }

                    &.changedOldTime {
                        color: #992609;
                        text-decoration: line-through;
                    }
                }
            }

            .info {
                color: #494949;

                &.noInfo {
                    color: #8d8d8d;
                }

                .redAlert {
                    color: #992609;
                }
            }
        }
    }
}

.locationQuery {
    width: calc(95% - 42px);

    margin: 0 auto;

    .locationQueryResult {
        padding: 16px;

        border-left: #000000 2px solid;

        &.Point {
            border-left-style: dotted;
        }

        &:first-child {
            margin-top: -8px;
        }

        p {
            margin: 0;

            font-family: $pFont;
        }
    }
}

.dropDown {
    p {
        text-align: center;
        font-family: $pFont;
    }
}
</style>

<style lang="scss">
p.quickOverview .mdi {
    margin-right: 4px;

    font-size: 20px;
}
</style>