<template>
    <section class="dynamicInfo" v-if="nearestStaionLoaded">
        <h1>Station {{ nearestStation.namen.lang }}</h1>
        <div class="departure" v-for="departure in departures" :key="departure.product.number">
            <p>
                <span class="lineProduct" :class="[ departure.product.categoryCode ]">
                    <span class="mdi mdi-train"></span>
                    {{ toFullName(departure.product.categoryCode) }}
                </span>
                <b>{{ departure.product.number }}</b>
            </p>
            <p>
                Spoor <b>{{ departure.plannedTrack }}</b>
            </p>
        </div>
    </section>
</template>

<script>
import stationsQueryable from "../../json/stationsSearchable.json";
import nsCall from "../../js/nsCall.js";
import vueChannel from "vue-channel";

var timer;

export default {
    data() {
        return {
            pos: {
                lat: 0,
                long: 0
            },
            departures: [],
            nearestStation: {},
            nearestStaionLoaded: false
        }
    },
    methods: {
        dynamicInfo() {
            if (this.pos.lat !== 0 && this.pos.long !== 0) {
                var stationDistances = stationsQueryable.map(station => {
                    var distanceLat = this.pos.lat - station.lat;
                    var distanceLong = this.pos.long - station.lng;

                    var directDistance = Math.sqrt(distanceLat ** 2 + distanceLong ** 2);

                    station.directDistance = directDistance;

                    return station;
                });

                stationDistances = stationDistances.sort((a, b) => a.directDistance - b.directDistance);

                var closestStation = stationDistances[0];

                // console.log(stationDistances);

                this.nearestStation = Object.assign({}, this.nearestStation, closestStation);
                this.nearestStaionLoaded = true;

                var stationCode = closestStation.code;

                nsCall("/departures/" + stationCode).then(response => {
                    var departures = response.payload.departures.map(
                        departure => {
                            return {
                                ...departure,
                                id: "r" + Math.random()
                            };
                        }
                    );

                    this.departures = departures;
                });

                return true;
            } else {
                return false;
            }
        },
        showDepartureTime(time) {
            var date = new Date(time);

            var fixedLengthNumber = number => {
                if (number < 10) {
                    return "0" + number;
                } else {
                    return number;
                }
            };

            return (
                fixedLengthNumber(date.getHours()) +
                ":" +
                fixedLengthNumber(date.getMinutes())
            );
        },
        viewDeparture(departure) {
            this.query = departure.product.number;

            this.performQuery();
        },
        asScheduled(departure) {
            return departure.actualDateTime === departure.plannedDateTime;
        },
        toFullName(shortName) {
            return shortName === "SPR" ? "Sprinter" : "Intercity";
        }
    },
    created() {
        vueChannel("pos").receive(state => {
            this.pos.lat = state.latitude;
            this.pos.long = state.longitude;
        });

        timer = setInterval(() => {
            this.dynamicInfo();
        }, 15000);

        const tryDynamicInfo = i => {
            if (i < 10) {
                var foundNearest = this.dynamicInfo();

                if (!foundNearest) {
                    setTimeout(() => {
                        tryDynamicInfo(i + 1);
                    }, 1000);
                }
            }
        }

        tryDynamicInfo(0);
    },
    destroyed() {
        clearInterval(timer);
    }
}
</script>

<style lang="scss" scoped>

@import "../../scss/config.scss";

h1 {
    margin: 16px 0;

    text-align: center;
    font-family: $hFont;
    font-weight: 400;
    color: #000000;
}

.departure {
    width: calc(95% - 32px);

    padding: 8px 16px;
    margin: 8px auto;

    p {
        margin: 0;

        font-family: $pFont;

        .lineProduct {
            margin-right: 8px;
            padding: 2px 8px;

            border-radius: 5px;

            &.IC {
                color: #000000;
                background-color: #cdf321;
            }

            &.SPR {
                color: #ffffff;
                background-color: #2196f3;
            }
        }
    }
}

</style>