<template>
    <div class="routeOption" @click="loadRoute(route)">
        <p>{{ route.directions.endName }}</p>
        <p class="quickOverview">
            <span class="startTime">{{ toTime(route.directions.departure_time.value) }}</span>
            <span class="mdi mdi-flag"></span>
            <span>
                <span
                    :class="tripStepIcon(step)"
                    v-for="step in route.directions.steps"
                    :key="'i' + step.id"
                ></span>
            </span>
            <span class="mdi mdi-flag-checkered"></span>
            <span class="endTime">{{ toTime(route.directions.arrival_time.value) }}</span>
        </p>
        <div class="stepOverview">
            <div
                :style="{ width: tripStepWidth(route.directions, step), borderBottomColor: tripStepBusColor(step) }"
                :class="tripStepPartClasses(step)"
                v-for="step in route.directions.steps"
                :key="step.id"
            ></div>
        </div>
    </div>
</template>

<script>
import {
    toTime,
    tripStepIcon,
    tripStepBusColor,
    tripStepPartClasses
} from "./shared.js";
import vueChannel from "vue-channel";
import { db } from "../../js/config.js";

export default {
    props: ["route"],
    methods: {
        toTime,
        tripStepIcon,
        tripStepBusColor,
        tripStepPartClasses,
        tripStepWidth(directions, step) {
            var fraction = step.duration.value / directions.totalForCalculation;

            return fraction * 100 + "%";
        },
        loadRoute(route) {
            var route = Object.assign({}, this.route, route);

            vueChannel("route").set({
                routeStarted: true,
                route
            });

            db.route = route;
        }
    }
};
</script>

<style lang="scss" scoped>
@import "../../scss/config.scss";

.routeOption {
    width: calc(100% - 32px);

    padding: 16px;

    p {
        min-height: 18px;

        font-size: 16px;
        font-family: $pFont;

        &.quickOverview {
            text-align: center;
        }

        .startTime {
            float: left;
        }

        .endTime {
            float: right;
        }

        & > .mdi {
            margin-right: 4px;

            font-size: 20px;
        }
    }

    .stepOverview {
        height: 2px;
        width: 100%;

        .step {
            height: 0;
            width: 20%;

            float: left;

            border-bottom: 2px dotted #9b9b9b;

            &.transport {
                border-bottom-style: dashed;

                &.train {
                    border-bottom-style: solid;

                    &.ic {
                        border-bottom-color: #cdf321;
                    }

                    &.spr {
                        border-bottom-color: #2196f3;
                    }
                }
            }
        }
    }
}
</style>